import * as React from "react";
import { Flex, Checkbox } from 'antd-mobile'

const CheckboxItem = Checkbox.CheckboxItem;
interface ISprops {
    [propsName: string]: any
}
interface IState {
    giveFlag: boolean,
    checkedArr: any,
    [stateName: string]: any,
    courseId: any
}

export default class ConsumerItem extends React.Component<ISprops, IState>{
    constructor(props: ISprops) {
        super(props);
        this.state = {
            checkedIndex: null,
            giveFlag: false,
            checkedArr: '',
            courseId: ''
        }
    }
    changeText = (type) => {
        //更改显示样式
        switch (type) {
            case 1:
                return '确认中'

            case 2:
                return '已成功'

            case 3:
                return '签到成功'

            case 4:
                return '已取消'

            default:
                return ''
        }

    }
    changeClass = (type) => {
        //更改显示样式
        switch (type) {
            case 1:
                return 'confirming'

            case 2:
                return 'apply-success'

            case 3:
                return 'sign-success'

            case 4:
                return 'cancel'

            default:
                return ''

        }

    }
    getCourseId(member){
        let arr: any = [];
        (member || []).map( (el,index) => {
            el.member_list.map( (req,i) => {
                if(req.checked){
                    arr.push(req.id)
                }
            })
        })
        this.setState({
            courseId: arr
        },() => this.props.change(this.state.courseId))
    }
    changeBox = (el, index) => {
        el.checked = !el.checked;
        const { member } = this.props;
        let list = member[index].member_list || [];
        let checkedIndex = false; //判断是否还有选中的状态

        for (let i = 0; i < list.length; i++) {
            let item = list[i];
            if (item.checked) {
                checkedIndex = true;
                continue;
            }
        }

        this.setState({
            checkedIndex: checkedIndex ? index : null,
        })
        this.getCourseId(member);
        
    }
    componentDidMount() {

    }
    render() {
        const { checkedIndex } = this.state;
        const { hadGiven, member } = this.props;
        return (
            <div className="consumer-item">

                {
                    member.map((el, index) => {
                        return (
                            <div className="detail" key={index}>
                                <div className="header">
                                    <p>申请时间：{el.add_time}</p>
                                    {
                                        el.erp_form_type === 2 || el.erp_form_type === 3 ?
                                            <p>{el.erp_form_type == 2 ? `试听时间：${el.st_time}` : `补课时间：${el.st_time}`}</p>
                                            :
                                            <div></div>
                                    }
                                </div>
                                {
                                    !el.card_no ?
                                    <div></div>
                                    :
                                    <Flex className="card">
                                        <Flex.Item>会员卡：{el.card_no}</Flex.Item>
                                        <Flex.Item>{el.price ? `-￥ ${el.price}` : `- ${el.num} 次`}</Flex.Item>
                                    </Flex>
                                }
                                {
                                    (el.member_list || []).map((els, indexs) => {
                                        return (
                                            <Flex key={indexs}>
                                                <Flex.Item>
                                                    {
                                                        hadGiven && els.status !== 3 && els.status !== 4?
                                                            <CheckboxItem
                                                                disabled={checkedIndex !== null && checkedIndex !== index}
                                                                key={els.id}
                                                                checked={els.checked}
                                                                onChange={() => this.changeBox(els, index)}
                                                            >
                                                                {els.member_name}  {els.member_phone}
                                                            </CheckboxItem>
                                                            :
                                                            `${els.member_name}  ${els.member_phone}`
                                                    }

                                                </Flex.Item>
                                                <Flex.Item className={this.changeClass(els.status)}>{this.changeText(els.status)}</Flex.Item>
                                            </Flex>
                                        )

                                    })
                                }
                            </div>
                        )
                    })
                }


            </div>

        )
    }

}
