import * as React from "react";
import { Button, Toast, Modal } from 'antd-mobile'
import qs from 'qs'

import CourseTitle from '../../components/courseTitle';
import ConsumerItem from '../../components/consumer/consumerItem'

import { getConsumerDetail, cancelApply } from '../../api/consumer'

import './consumer.less'

const alert = Modal.alert;
interface ISprops {
    [propsName: string]: any
}
interface IState {
    hasTags: boolean,
    hadGiven: boolean,
    courseInfo: any,
    courseMemberList: any,
    course_member_id: any,
    courseId: any,
    memberId: any,
    stuType: any
}

export default class BindPhone extends React.Component<ISprops, IState>{
    constructor(props: ISprops) {
        super(props);
        this.state = {
            hasTags: true,
            hadGiven: false,
            courseInfo: {},
            courseMemberList: [],
            course_member_id: '',
            courseId: '',
            memberId: '',
            stuType: ''
        }
    }
    pressFlag: boolean = true;
    componentWillMount() {
        document.title = '消费详情';
        this.getDetailList()
    }
    getDetailList() {
        Toast.loading('')
        let courseId = this.props.match.params.id;
        let search = this.props.location.search;
        let data = qs.parse(search.substring(1));
        this.setState({
            courseId: courseId,
            memberId: data.member_id
        })
        getConsumerDetail({
            courseId: courseId
        }).then((data) => {
            Toast.hide()
            if (!data.status) {
                this.setState({
                    courseInfo: data.data.courseInfo,
                    courseMemberList: data.data.courseMemberList,
                    stuType: data.data.stuType
                })
            } else {
                Toast.fail(data["message"],2)
            }
        })
    }
    getCourseList = (arr) => {
        let str = arr.join(',')
        this.setState({
            course_member_id: str
        })
    }
    handleGive = () => {
        const { hadGiven } = this.state;
        if (!hadGiven) {
            this.setState({
                hadGiven: true,
            })
        }

    }
    pressGive = () => {
        this.pressFlag = false;
        const { course_member_id } = this.state;
        Toast.loading('')
        cancelApply({
            course_member_id,
            send_type: 2
        }).then((data) => {
            this.pressFlag = true;
            Toast.hide()
            if (!data.status) {
                Toast.info('放弃报名成功',3)
                this.props.history.go(-1)
            } else {
                Toast.fail(data["message"],2)
            }
        })
    }
    clickSkip = () => {
        const { courseInfo } = this.state;
        this.props.history.push(`/consumerCourse/${courseInfo.id}`)
    } 
    submitGive = () => {
        const { course_member_id } = this.state;
        if (course_member_id.replace(/\s/g, '').length === 0) {
            Toast.fail('尚未选择放弃的课程',2)
            this.setState({
                hadGiven: false,
            })
        } else {

            alert('提示', '确定要取消你的席位吗？操作取消后，将无法撤回', [
                { text: '取消', onPress: () => {return false} },
                {
                    text: '确定',
                    onPress: this.pressFlag ? this.pressGive : () => {}
                },
            ])

        }

    }
    render() {
        const { hasTags, hadGiven, courseInfo, courseMemberList, courseId, memberId, stuType  } = this.state;
        return (
            <div className="consumer-detail" style={{ paddingBottom: '1rem' }}>
                <CourseTitle 
                    hasTags={hasTags} 
                    course={courseInfo} 
                    courseId={courseId} 
                    memberId={memberId} 
                    click={this.clickSkip}
                    stuType={stuType}
                />
                <ConsumerItem 
                    hadGiven={hadGiven} 
                    member={courseMemberList} 
                    change={this.getCourseList} 
                />

                <div className="give-up">
                    {
                        hadGiven ?
                            <Button type="primary" className="give-btn" onClick={this.submitGive}>确认放弃</Button>
                            :
                            <Button type="primary" className="give-btn" onClick={this.handleGive}>放弃报名</Button>

                    }

                    <p>为了避免席位不足若申请换人，请联系021-51847882</p>
                </div>
            </div>
        )
    }

}
